<template>
<div class="header">
    <div class="content-wrapper">
        <div class="avatar">
            <img width="64" height="64" :src="seller.avatar">
        </div>
        <div class="content">
            <div class="title">
                <span class="brand"></span>
                <span class="name">{{seller.name}}</span>
            </div>
            <div class="description">
                {{seller.description}}/{{seller.deliveryTime}}分钟送达
            </div>


            <div v-if="seller.supports" class="supports">
                <icon :size="1" :supports="seller.supports" :top="1"></icon>
            </div>
       
        </div>
        <div v-if="seller.supports" class="support-count" @click="showDetail">
            <span class="count">{{seller.supports.length}}</span>
            <i class="icon-keyboard_arrow_right"></i>
        </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetail">
        <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
        <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
        <img :src="seller.avatar" width="100%" height="100%">
    </div>
    <transition name="fade">
    <div class="detail" v-show="detailShow" @click="closeDetail">
        <!--使用sticky footer布局 解决 内容不够 长的时候，页脚在视窗底部， 内容足够长，页脚会被内容向下推送-->
        <div class="detail-wrapper clearfix">
            <div class="detail-main">
                <h1 class="name">{{seller.name}}</h1>
             
                <div class="star-wrapper">
                     <star :size="36" :score="seller.score"></star>
                </div>
                <div class="title">
                    <div class="line"></div>
                    <div class="text">优惠信息</div>
                    <div class="line"></div>
                </div>
                <div v-if="seller.supports">
                  <icon :size="2" :supports="seller.supports"></icon>
                </div>


                 <div class="title">
                    <div class="line"></div>
                    <div class="text">商家公告</div>
                    <div class="line"></div>
                </div>

                <div class="bulletin">
                    <p>{{seller.bulletin}}</p>
                </div>
            </div>
        </div>
        <div class="detail-close" @click="closeDetail"><i class="icon-close"></i></div>
    </div>
    </transition>
</div>
</template>

<script type="text/ecmascript-6">
import star from "../../components/star/star";
import icon from "../../components/icon/icon";
export default {
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      detailShow: false
    };
  },
  methods: {
    showDetail() {
      this.detailShow = true;
    },
    closeDetail() {
      this.detailShow = false;
    }
  },
  components: {
    star,
    icon
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixin';

.header {
  color: #ffffff;
  position: relative;
  background: rgba(7, 17, 27, 0.5);
  overflow: hidden;

  .content-wrapper {
    position: relative;
    padding: 24px 12px 18px 24px;
    font-size: 0;

    .avatar {
      display: inline-block;
      vertical-align: top;

      img {
        border-radius: 2px;
      }
    }

    .content {
      display: inline-block;
      margin: 0 0 0 16px;
      font-size: 14px;

      .title {
        margin: 2px 0 8px 0;

        .brand {
          display: inline-block;
          vertical-align: top;
          width: 30px;
          height: 18px;
          bg-image('brand');
          background-size: 30px 18px;
          background-repeat: no-repeat;
        }

        .name {
          margin: 0 0 0 6px;
          font-size: 16px;
          line-height: 18px;
          font-weight: bold;
        }
      }

      .description {
        margin: 0 0 10px 0;
      }

      .supports {
        .support-item {
          margin-bottom: 12px;
          font-size: 0;

          &:last-child {
            margin-bottom: 0;
          }
        }

        .text {
          vertical-align: top;
          line-height: 12px;
          font-size: 10px;
          font-weight: 700;
          color: #fff;
        }
      }
    }

    .support-count {
      position: absolute;
      line-height: 24px;
      right: 12px;
      bottom: 14px;
      padding: 2px 10px;
      height: 24px;
      border-radius: 14px;
      background: rgba(0, 0, 0, 0.2);
      text-align: center;

      .count {
        vertical-align: top;
        font-size: 10px;
      }

      .icon-keyboard_arrow_right {
        line-height: 24px;
        margin: 0 0 0 2px;
        font-size: 10px;
      }
    }
  }

  .bulletin-wrapper {
    position: relative;
    height: 28px;
    line-height: 28px;
    padding: 0 22px 0 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background: rgb(7, 17, 27, 0, 0.2);

    .bulletin-title {
      display: inline-block;
      height: 12px;
      width: 22px;
      vertical-align: top;
      margin-top: 8px;
      bg-image('bulletin');
      background-size: 22px 12px;
      background-repeat: no-repeat;
    }

    .bulletin-text {
      font-size: 10px;
      margin: 0 4px;
      vertical-align: top;
    }

    .icon-keyboard_arrow_right {
      position: absolute;
      font-size: 10px;
      top: 8px;
      right: 12px;
    }
  }

  .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    filter: blur(10px);
  }

  .detail {
    position: fixed;
    z-index: 100;
    overflow: auto;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    backdrop-filter: blur(10px);
    background: rgba(7, 17, 27, 0.8);
    opacity: 1;

    &.fade-enter-active, &.fade-leave-active {
      transition: all 0.5s;
    }

    &.fade-enter, &.fade-leave-active {
      opacity: 0;
      background: rgba(7, 17, 27, 0);
    }

    .detail-wrapper {
      min-height: 100%;
      width: 100%;

      .detail-main {
        margin-top: 64px;
        padding-bottom: 64px;

        .name {
          text-align: center;
          line-height: 16px;
          font-weight: 700;
          font-size: 16px;
        }

        .star-wrapper {
          margin-top: 18px;
          padding: 2px 0;
          text-align: center;
        }

        .title {
          display: flex;
          width: 80%;
          margin: 28px auto 24px auto;

          .line {
            flex: 1;
            position: relative;
            top: -6px;
            border-bottom: 1px solid rgba(225, 225, 255, 0.2);
          }

          .text {
            padding: 0 12px;
            font-size: 14px;
          }
        }

        .supports {
          width: 80%;
          margin: 0 auto;

          li {
            padding: 0 12px;
            margin-bottom: 12px;
            font-size: 0;

            &:last-child {
              margin-bottom: 0;
            }

            .text {
              font-size: 12px;
              line-height: 16px;
            }
          }
        }

        .bulletin {
          width: 80%;
          margin: 0 auto;

          p {
            padding: 0 12px;
            line-height: 24px;
            font-size: 12px;
          }
        }
      }
    }

    .detail-close {
      position: relative;
      width: 32px;
      height: 32px;
      margin: -64px auto 0 auto;
      font-size: 32px;
      text-align: center;
      line-height: 32px;
      clear: both;
    }
  }
}
</style>



