<template>
  <div class="ratingselect">
	  <div class="rating-type">
		  <span @click="select(2,$event)" class="block positive" :class="{'active':selectType===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
		  <span  @click="select(0,$event)" class="block positive" :class="{'active':selectType===0}">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
		  <span  @click="select(1,$event)" class="block negative" :class="{'active':selectType===1}">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
	  </div>
	  <div  @click="toggleContent" class="swith" :class="{'on':onlyContent=onlyContent}">
		  <span class="icon-check_circle"></span><span class="text" >只看内容的评价</span>
		  <ul class="text">
			  <li>

			  </li>
		  </ul>
	  </div>
  </div>
</template>

<script type="text/ecmascript-6">
const POSITIVE = 0; //正向评价
const NEGATIVE = 1; //负面评价
const ALL = 2;
export default {
  props: {
    ratings: {
      type: Array,
      default() {
        return [];
      }
    },
    selectType: {
      type: Number,
      default: ALL
    },
    onlyContent: {
      type: Boolean,
      default: false
    },
    desc: {
      type: Object,
      default() {
        return {
          all: "全部",
          positive: "满意",
          negative: "不满意"
        };
      }
    }
  },
  computed: {
    positives() {
			//filter 它用于把Array的某些元素过滤掉,然后返回剩下的元素
      //把data.json 中 ratings 遍历ratings.rateType===0 拿到正向评价
      return this.ratings.filter(ratings => {
        return ratings.rateType === POSITIVE;
      });
    },
    negatives() {
      return this.ratings.filter(ratings => {
        return ratings.rateType === NEGATIVE;
      });
    }
  },
  methods: {
    select(type, event) {
      if (!event._constructed) {
        return;
      }
      this.$emit("select", type);
    },
    toggleContent(event) {
      if (!event._constructed) {
        return;
      }
      this.$emit("toggle");
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixin.styl';

.ratingselect {
	.rating-type {
		padding: 18px 0;
		margin: 0 18px;
		border-1px(rgba(7, 17, 27, 0.1));

		.block {
			display: inline-block;
			padding: 8px 12px;
			border-radius: 2px;
			margin-right: 8px;
			font-size: 12px;
			color: rgb(77, 85, 93);

			&.active {
				color: #fff;
			}

			.count {
				display: inline-block;
				font-size: 8px;
				margin-left: 2px;
				line-height: 16px;
			}

			&.positive {
				background: rgba(0, 160, 220, 0.2);

				&.active {
					background: rgb(0, 160, 220);
				}
			}

			&.negative {
				background: rgba(77, 85, 93, 0.2);

				&.active {
					background: rgb(77, 85, 93);
				}
			}
		}
	}

	.swith {
		padding: 12px 18px;
		line-height: 24px;
		border-bottom: 1px solid rgba(7, 17, 27, 0.1);
		color: rgb(147, 153, 159);
		font-size: 0;

		&.on {
			.icon-check_circle {
				color: #00c850;
			}
		}

		.icon-check_circle {
			display: inline-block;
			vertical-align: top;
			margin-right: 4px;
			font-size: 24px;
		}

		.text {
			display: inline-block;
			vertical-align: top;
			font-size: 12px;
		}
	}
}
</style>


